.game-list {
  position: relative;
  z-index: 99;
}
.game- {
  position: relative;
  z-index: 99;
}
.DivText {
	width:150px;margin:auto;background-color:black;color:white;font-size:10px;text-transform:uppercase;box-shadow: 0 -2px 5px rgba(170, 115, 65, 0.7);position:relative
}
.DivTimer{
	background-color:black;width:220px;margin:auto;border-radius:20px;box-shadow: 0 0 10px rgba(170, 115, 65, 0.7);position:relative
}
.DivClock{
	font-size:24px;color:rgb(255, 200, 150);text-shadow: 0px 0px 7px #ffffdd, 0 0 5em rgb(255, 200, 150);z-index:1;margin-top:-35px
}
.DivJackpot{
	width:150px;margin:auto;background-color:black;color:white;font-size:16px;text-transform:uppercase;box-shadow: 0 2px 5px rgba(170, 115, 65, 0.7);position:relative
}
.TotalInvested{
	text-transform:uppercase;text-shadow:0px 0px 7px rgb(170, 115, 65), 0 0 5em rgb(170, 115, 65);font-size:20px;color:rgb(255, 200, 150);text-align:center
}
input[type="text"] {
	background: #3b3b3b;
	border: 2px solid #747576;
	height: 35px;
	width: 80%;
	border-radius: 50px;
	padding: 5px 15px;
	color: #fff;
	margin-top: 18px;
	font-size: 16px;
}